import cookies from 'browser-cookies';

export default {
  set(name, key, value) {
    cookies.set(
      `${name}_${key}`,
      JSON.stringify(value),
      { expires: 365 }
    );
  },
  get(name, key, defaultValue) {
    try {
      const value = cookies.get(`${name}_${key}`);
      //   ,     7 
      //  Safari  Brave.
      if (value) {
        const parsedValue = JSON.parse(value);
        this.set(name, key, parsedValue);
        return parsedValue;
      }
      return defaultValue;
    } catch  {
      return defaultValue;
    }
  }
};
